body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/* FLEX */
.flex { display: flex; }

.horizontal, .horizontal-only { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }

.flex-center {
  justify-content: center;
  align-items: center; }
.flex-start {
  justify-content: flex-start;
  align-items: center;
}

/* font */
.text-center {
  text-align: center; }

/* container */
.container {
  padding: 10px;
  width: calc(100% - 20px);
  max-width: 1440px;
  margin: 0 auto;
}
@media screen and (max-width: 720px){
  .overflow-x {
    overflow-x: scroll; }
  .text-center-mb {
    text-align: center; }
}
/* ITENS */
.full-height { height: 100%; }
.full-width { width: 100%; }

.padding-vertical-0 { padding: 0; }
.padding-bottom-10 { padding-bottom: 10px; }

/* COMPONENTS */
.topbar {
  background-color: #1B6499;
  height: 5px; }

.card-user,
.card-wishlist,
.card-cart {
  height: 50px;
  cursor: pointer;
  padding-bottom: 10px; }

.card-user{ 
  background-color: #FEC401; }
.card-wishlist {
  background-color: #1B6499; }
.card-cart {
  background-color: #064A7B; }

.carrinho-fonte {
  color: #FEC401; }
.color-white {
  color: #FFF; }

.input-pesquisa {
  width: 70%;
  height: 20px;
  margin: 10px 0 0 0;
  border: 1px solid #cccc;
  border-radius: 10px;
  border-right: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;

  font-size: 1rem;
  padding: 10px 15px; }
.btn-pesquisa {
  cursor: pointer;
  color: #064A7B;
  height: 42px;
  font-size: 1rem;
  margin: 10px 0 0 0;
  background-color: transparent;  
  border: 1px solid #cccc;
  border-radius: 10px;
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0; }

@media screen and (max-width: 720px){
  .logo {
    padding: 15px 0; }
  .input-pesquisa {
    width: 95%;
    margin-bottom: 10px; }
  .card-user,
  .card-wishlist,
  .card-cart  {
    height: 40px;
    padding-bottom: 0;
  }
}

/* Categorias */

.Categorias {
  background-color: #E5E7EA;
  border-top: #FEC401 2px solid;
  color: #385984;
  font-weight: bold;
  font-size: 0.9rem; }
.Categorias .card-blue {
  background-color: #1B6499;
  color: #FFF; }
.Categorias > div > div {
  padding: 10px 0;
  cursor: pointer; }

@media screen and (max-width: 720px){
  .Categorias > div > div {
    padding: 10px;
    cursor: pointer; }
  .padding-0-mb {
    padding: 0; 
    width: 100%;
  }
}

/* HeroShop */
.HeroShop {
  padding: 30px 0; }
.breadcrumbs {
  font-size: 0.8rem;
  color: #777; }

.main-pic {
  max-width: 600px; }
.min-pic {
  max-width: 100px; }

.card-detalhes {
  padding: 30px;
  background-color: rgb(249, 249, 249);
  box-shadow: 0px 0px 5px #7777;
  margin-top: 30px; }
.titulo h2 {
  margin: 0;
  font-size: 1.9rem;
  font-weight: 300; }
.titulo small {
  font-size: 0.9rem;
  color: #777; }
.marca h4 {
  margin: 7px 0;
  font-size: 0.9rem;
  color: #555; }
.estrelas {
  padding: 10px 0; }

.price {
  font-size: 1.7rem;
  font-weight: bold; }
.price span > span {
  font-size: 1rem; }

.quantidade {
  margin: 20px 0 10px 0; }
.quantidade .label {
  font-size: 0.9rem;
  color: #444;
  padding-right: 10px; }
.quantidade-input {
  -moz-appearance:textfield;
  background-color: #064A7B;
  color: #FFF;
  font-weight: bold;
  font-size: 0.8rem;
  padding: 2px 5px 2px 25px;
  width: 28px;
  border-radius: 20px;
  border: 0;
  margin-left: -18px; }
.change-button {
  border-radius: 50px;
  /* background-color: rgb(2, 55, 92); */
  /* color: #fff; */
  background-color: #eee;
  color: rgb(2, 55, 92);
  width: 10px;
  height: 10px;
  padding: 2px;
  margin-top: 3px;
  cursor: pointer;
  line-height: 7px;
  z-index: 100; }
.sub-button {
  margin-left: -17px; }

.full-price {
  color: #444;
  font-size: 0.9rem; }
.full-price .price {
  font-size: 1.1rem;
  font-size: #222;
}

.btn.btn-cta {
  background-color: #064A7B;
  color: #FFF;
  font-size: 1.1rem;
  font-weight: bold;
  margin: 20px 0;
  padding: 15px 25px;
  border-radius: 10px;
  border: 0;
  cursor: pointer;
}
.btn.btn-cta:hover {
  background-color: rgb(4, 51, 85);
}

.cep {
  font-size: 1rem;
  color: #222;
  border-radius: 10px;
  border: 1px solid #cccc;
  padding: 5px 10px;
}
.button-cep {
  margin: 0px 10px;
  border: 0;
  border-radius: 10px;
  font-size: 0.8rem;
  color: #FFF;
  background-color: rgb(152, 152, 152);
  font-weight: bold;
  cursor: pointer;
}
.button-cep:hover {
  background-color: rgb(121, 121, 121);
}

@media screen and (max-width: 720px){
  .HeroShop {
    padding: 10px 0; }
  .cep {
    width: 60%; }
}

/* DESCRICAO */
.descricao-header {
  display: flex;
  color: rgba(113, 113, 113, 0.824);
  font-size: 1.3rem;
  border-bottom: 2px solid #064A7B;
  margin-bottom: 50px; }
.descricao-item {
  padding: 20px 15px;
  cursor: pointer; }
.descricao-item-active {
  color: #064A7B;
  font-weight: bold;
  border-bottom: #064A7B 3px solid; }
td {
  font-size: 1rem;
  padding: 15px;
  border-bottom: 1px solid #aaaa; }
tr > td:first-child {
  font-weight: bold; }

@media screen and (max-width: 720px){
  .table {
    width: 100%; }
  .table td {
    padding: 5px; }
}

/* Avaliacoes */ 
.Avaliacoes {
  padding: 40px 0; }
.card {
  margin: 15px;
  padding: 15px;
  /* border: 1px solid #aaaa; */
  box-shadow: 0px 0px 5px #aaaa;
  border-radius: 10px; }
.card-body {
  height: 60px; }
.cor-yellow {
  color: #EDB40F; }

.RedesSociais {
  background-color: #064A7B;
  color: #FFF; }
.RedesSociais .icon {
  padding: 15px;
  cursor: pointer; }

.Newsletter {
  background-color: #EDB40F;
  padding: 30px 0; }
.Newsletter h3 {
  color: #FFF;
  font-size: 1.7rem;
  margin: 0; }
.Newsletter p {
  font-size: 1rem; }

input[type=radio] {
  padding-left:5px;
  padding-right:5px;
  border-radius:15px;
  -webkit-appearance:button;
  border: double 2px rgba(255, 255, 255, 0);
  background-color:#ffffff;
  color:#FFF;
  white-space: nowrap;
  overflow:hidden;
  width:20px;
  height:20px;
  cursor: pointer; }
input[type=radio]:checked {
  background-color:rgb(189, 143, 5); }
.news-input {
  font-size: 1rem;
  height: 20px;
  padding: 10px 15px;
  border-radius: 10px;
  border: 0;
  margin: 20px 10px 10px 0; }
.btn-news {
  background-color: #1B6499;
  color: #FFF;
  font-size: 1rem;
  border: 0;
  padding: 10px 15px;
  font-weight: bold;
  border-radius: 20px; }

@media screen and (max-width: 720px){
  .news-input {
    width: 100%; }
  .btn-news {
    margin-top: 20px; }
}

/* Institucional */
.Institucional {
  background-color: #F0F0F0;
  padding: 25px 0; }
.titulo-institucional {
  color: #353535;
  font-size: 0.6rem;
  font-weight: bold; }
.tens-institucionais .item {
  color: #000;
  font-size: 0.7rem;
  font-weight: 300;
  padding: 3px 0;
  cursor: pointer; }

@media screen and (max-width: 720px){
  .tens-institucionais {
    margin-bottom: 20px; }
}

/* Footer */
.Footer {
  background-color: #111;
  padding: 20px 0;
  color: #FFF; }